import React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import styles from './link.module.css';

export const Link = ({ to, children }) =>
  <span
    className={ styles.link }
  >
    <GatsbyLink
      to={to}
      activeClassName={styles.active}
    >
      { children }
    </GatsbyLink>
  </span>